import OpenIcon from "@material-ui/icons/Launch";
import MaterialTable from "material-table";
import React, { useEffect, useState } from "react";
import { useHistory } from "react-router-dom";
import { getApplications } from "../../redux/actions/applications_actions";

const columns = [
  {
    title: "",
    field: "name",
  },
  {
    title: "",
    field: "state",
  },
  {
    title: " ",
    field: "phoneNumber",
  },
  {
    title: " ",
    field: "passportNumber",
  },
  {
    title: "",
    field: "destination",
  },
  {
    title: " ",
    field: "flightDate",
  },
  {
    title: " ",
    field: "testDate",
  },
];

export default function Applications(props) {
  const history = useHistory();

  const [applications, setApplications] = useState([]);

  useEffect(() => {
    const fetchData = async () => {
      const result = await getApplications();
      if (result) {
        result.forEach((application) => {
          switch (application.state) {
            case "registered":
              application.state = " ";
              break;

            case "tested":
              application.state = " ";
              break;

            case "result issued":
              application.state = "  ";
              break;

            case "result delivered":
              application.state = " ";
              break;
            default:
              break;
          }
        });
        setApplications(
          result.map((app) => ({
            ...app,
            flightDate: new Date(app.flightDate).toLocaleDateString(),
            testDate: new Date(app.testDate).toLocaleDateString(),
          }))
        );
      }
    };
    fetchData();
  }, []);

  return (
    <div style={{ margin: "50px", width: "75vw" }}>
      <MaterialTable
        title=" "
        data={applications}
        columns={columns}
        options={{
          exportButton: true,
          grouping: true,
          // selection: true,
          rowStyle: {
            backgroundColor: "#EEE",
          },
        }}
        localization={{
          toolbar: {
            searchPlaceholder: "",
            searchTooltip: "",
            exportTitle: " ",
            exportName: " ",
            exportAriaLabel: " ",
            nRowsSelected: "    {0} ",
          },
          pagination: {
            labelDisplayedRows: " {from}  {to}    {count}",
            lastTooltip: "",
            firstTooltip: "",
            previousTooltip: "",
            nextTooltip: "",
            labelRowsSelect: "",
          },
          header: {
            actions: "",
          },
          body: {
            emptyDataSourceMessage: "  ",
            filterRow: {
              filterTooltip: "",
            },
            deleteTooltip: "",
            editTooltip: "",
          },
          grouping: {
            placeholder:
              "          ",
          },
        }}
        actions={[
          {
            icon: () => <OpenIcon />,
            tooltip: " ",
            onClick: (event, data) => {
              history.push(`/application/${data._id}`);
            },
          },
        ]}
      />
    </div>
  );
}
